#range::-webkit-slider-runnable-track {
  width: 300px;
  height: 10px;
  background: linear-gradient(
    to right,
    blue 25%,
    green 25%,
    green 50%,
    yellow 50%,
    yellow 75%,
    red 75%,
    red 80%
  );
  border: none;
  border-radius: 3px;
}

#range::-moz-range-track {
  width: 300px;
  height: 10px;

  background: linear-gradient(
    to right,
    blue 25%,
    green 25%,
    green 50%,
    yellow 50%,
    yellow 75%,
    red 75%,
    red 100%
  );
  border: none;
  border-radius: 3px;
}

.main {
  width: 40%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  opacity: 0.9;
}

@media screen and (max-width: 768px) {
  .main {
    width: 100%;
  }
}
